import React from 'react'
import { graphql } from 'gatsby'
import { Jumbotron } from 'reactstrap'
import { Helmet } from 'react-helmet'

import Toc from '../components/toc'
import Layout from '../components/layout'

export default blogComponent

function blogComponent({ data, location }) {
  const blog = data.markdownRemark

  const title = `Blog de lyceum | ${blog.frontmatter.title}`
  const description = `Un article de ${blog.frontmatter.author} intitulé: «${blog.frontmatter.title}» publié sur le blog de lyceum, un site libre pour le lycée.`

  return (
    <Layout location={location} data={data}>
      <Helmet
        title={`${title}`}
        meta={[
          {
            name: `description`,
            description: `${description}`,
          },
          {
            name: `keywords`,
            content: `blog`,
          },
        ]}
      ></Helmet>
      <Jumbotron>
        <h1 className="display-3">{`${blog.frontmatter.title}`}</h1>
        <Toc tableOfContents={blog.tableOfContents} />
      </Jumbotron>
      <div dangerouslySetInnerHTML={{ __html: blog.html }} />
    </Layout>
  )
}

export const query = graphql`
  query MDblogQuery($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      tableOfContents(maxDepth: 3)
      timeToRead
      fields {
        slug
      }
      frontmatter {
        author
        tags
        title
        date
      }
    }
  }
`
